import React, {Fragment, useState} from "react";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCoffee, faUserClock, faTruck, faCheese, faClipboardList, faHome } from '@fortawesome/free-solid-svg-icons'
import { Nav, Dropdown, NavItem, NavLink } from "react-bootstrap";


const AdminNav = () => {


    const [iconSize, setIconSize] = useState("2x");


    return (
    <Fragment>

        <div className="navItem raleway-font "> 
            <div className="icon">
                <FontAwesomeIcon size={iconSize} icon={faHome} />
            </div>
    
            <Nav.Link className="nav_links" href="/admin/main"  > Home </Nav.Link>

        </div>

        <div className="navItem"> 
            <div className="icon" > 
                <FontAwesomeIcon size={iconSize} icon={faCheese} />
            </div>
            

             <Dropdown as={NavItem} className="raleway-font">
                <Dropdown.Toggle className="nav_links" as={NavLink} >Products</Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item className="nav_links dropDown" href="/admin/product/add-product" >Add Products</Dropdown.Item>
                    <Dropdown.Item className="nav_links dropDown" href="/admin/product/remove-product" >Remove Products</Dropdown.Item>
                    <Dropdown.Item className="nav_links dropDown" href="/admin/product/edit-product" >Edit Products</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>

        </div>

        <div className="navItem raleway-font" > 
            <div className="icon" >
                <FontAwesomeIcon size={iconSize} icon={faClipboardList} />
            </div>
      
            <Nav.Link className="nav_links" href="/admin/inventory" > Inventory </Nav.Link>

        </div>

        <div className="navItem raleway-font" > 
            <div className="icon">
                <FontAwesomeIcon size={iconSize} icon={faTruck} />
            </div>
        
   

            <Dropdown as={NavItem} className="raleway-font" >
                <Dropdown.Toggle className="nav_links" as={NavLink}>Orders</Dropdown.Toggle>
                <Dropdown.Menu>
                    <Dropdown.Item className="nav_links dropDown" href="/admin/pending-orders" >Pending Orders</Dropdown.Item>
                    <Dropdown.Item className="nav_links dropDown" href="/admin/fulfilled-orders" >Fulfilled Orders</Dropdown.Item>
                </Dropdown.Menu>
            </Dropdown>

        </div>

        <div className="navItem raleway-font "> 
            <div className="icon" >
                <FontAwesomeIcon size={iconSize} icon={faUserClock} />
            </div>

        
             <Nav.Link href="/admin/employee" className="nav_links" > Employee </Nav.Link>
        </div>

        <div className="navItem raleway-font " style={{border: "none"}} > 
             <Nav.Link href="/" className="nav_links" > Go Back </Nav.Link>
        </div>


    </Fragment>
    )
}

export default AdminNav;