import React, {useState, useEffect} from "react";
import AdminNav from "../Part1";
import TopPanel from "./InventoryTop";
import Nav from "../../LeftSide/LeftSideNav";
import axios from "axios";


import {Table} from "react-bootstrap";

const RemoveProduct = () => {

    const [products, setProducts] = useState([]);

    let links = {
        meat: "/admin/product/remove-product/meat",
        snack: "/admin/product/remove-product/snack",
        vegetable: "/admin/product/remove-product/vegetable",
        cheese: "/admin/product/remove-product/cheese",
        fruit: "/admin/product/remove-product/fruit"
    }

    useEffect(() => {
        let url = window.location.pathname + window.location.search;


        switch (url) {
            case "/admin/product/remove-product":
                axios.get("/inventory/all")
                .then(res => {
                    setProducts(res.data);
                })
                break; 
            case links.meat:
                axios.get("/inventory/meat")
                .then(res => {
                    setProducts(res.data);
                })
                break;
            case links.snack: 
                axios.get("/inventory/snack")
                .then(res => {
                    setProducts(res.data);
                })
                break;
            case links.vegetable:
                axios.get("/inventory/vegetable")
                .then(res => {
                    setProducts(res.data);
                })
                break;
            case links.cheese:
                axios.get("/inventory/cheese")
                .then(res => {
                    setProducts(res.data);
                })
                break;
            //Add link to fruit route
        
            default:
                break;
        }
    });

    //Function for deleting product
    const deleteProduct = (id) => {
        let data = {
            id: id
        }
        axios.post("/inventory/delete-product", data)
        .then(res => {
            if(res.data) {
                window.location.reload();
            }
        })
    }

    return (
        <div className="admin_container" >
            <AdminNav />
            <div className="admin_main_content" >
                <TopPanel />
                <Nav links={links} />
                
        <Table striped bordered hover size="sm">
            <thead>
                <tr>
                    <th> Product Name </th>
                    <th> Product Price </th>
                    <th> Product Category </th>
                    <th> Product Stock </th>
                    <th> Remove Product </th>
                </tr>
            </thead>
            <tbody>
                {products.map((item) => {
                    return (
                        <tr>
                            <td>
                                {item.name}
                            </td>
                            <td>
                                {item.price}
                            </td>
                           <td>
                               {item.category}
                           </td>
                           <td>
                               {item.stock}
                           </td>
                           <td>
                               <a onClick={deleteProduct.bind(this, item._id)} href="#" > Remove </a> 
                           </td>
                        </tr>
                    )
                })}
            </tbody>
        </Table>

            </div>
        </div>
    )
}


export default RemoveProduct;