import React, {useEffect, useState} from "react";
import AdminNav from "../Part1";
import axios from "axios";
import {Table} from "react-bootstrap";


const FulfilledOrders = () => {

    const [orders, setOrders] = useState([]);


    useEffect(() => {
        axios.get("/orders/total/fulfilledOrders")
        .then(res => {
            console.log(res);

            setOrders(res.data);
        })
    }, [])

    return (
        <div className="admin_container" >
            <AdminNav />
            <div className="admin_main_content" >
                <div className="pendingOrdersTitleDiv" >
                    Fulfilled Orders
                </div>
                <div className="table_fulfilled_orders" >
                <Table striped bordered hover>
                    <thead>
                        <tr>
                        <th>Product Id</th>
                        <th>Product Price</th>
                        <th>Qty</th>
                        <th>Session Id</th>
                        <th> Order Status </th>
                        </tr>
                    </thead>
                    <tbody>
                       
                        {
                            orders.map((item) => {
                                return (
                                    <tr>
                                        <td> {item.product_id} </td>
                                        <td> {item.price} </td>
                                        <td> {item.qty} </td>
                                        <td> {item.ssn_id} </td>
                                        <td> Order Fulfilled </td>
                                    </tr>
                                )
                            })
                        }
                    </tbody>
                    </Table>
                </div>

            </div>
        </div>
    )
}


export default FulfilledOrders;