import React from "react";

//Components import 
import AdminNav from "../Part1";
import Table from "../Table";
//End of Components import

const Employee = () => {

    let headNames = {
        one: "Employee Name",
        two: "Department",
        three: "Customer Ratings",
        four: "Email"
    }

    return (
        <div className="admin_container" >
            <AdminNav />
            <div className="admin_main_content">
                <h3> Employee List </h3>
                <div>
                    <Table headNames={headNames} />
                </div>
            </div>
        </div>
    )
}


export default Employee;