import React, {Fragment, useState, useEffect} from "react";
import "../../static/css/Left/Card.css";
import axios from "axios";




const GroceryCards = (props) => {

    useEffect(() => {
        console.log("The props image");
        console.log(props.image);
    }, [])

    const [shoppingCart, setShoppingCart] = useState({});

    const handleClick = () => {
        //New Cart object
        const new_cart_item = {
              name: props.name,
              price: props.price,
              id: props.id,
              stock: props.stock
        }

        //Axios post request to send data back to the backend in which it turns it into an array
        axios.post("/add-to-cart", new_cart_item)
        .then(res => {
            console.log(res);
        })
    }
    
    let url = `../../static/images/${props.image}`;

    let imageStyles = {
        backgroundImage: "url(../../static/images/" + props.image + ")",
        backgroundSize: "cover",
        backgroundPosition: "center"
    }

 

    return (
    <div class="card_container">
        
        {/* <div style={imageStyles} class="card_image">

        </div> */}

        <img class="card_image" src={require(`../../static/images/${props.image}`)} />


        <div class="product_description_div">
            <div>
                <p> {props.name} </p>
            </div>
            <div>
                <p> Price: ${props.price} </p>
            </div>
            <div>
                <button onClick={handleClick} > Add to Cart </button>
            </div>

        </div>
    </div>

    )
}



export default GroceryCards;
