import React, {useEffect, useState} from "react";
import { useParams } from "react-router-dom";
import axios from "axios";

import AdminNav from "../Part1";

const EditProduct = () => {

    let { productId } = useParams();

    const [product, setProduct] = useState([]);

    //State Values of inputs
    const [productNameInput, setProductNameInput] = useState();
    const [productPriceInput, setProductPriceInput] = useState();
    const [productCategory, setProductCategory] = useState();
    const [productTags, setProductTags] = useState();
    const [productDesc, setProductDesc] = useState();
    //End of state values of inputs


    //State for whether something is updated or not
    const [updated, setUpdated] = useState(false);
    

    let data = {
        productId: productId
    }

    useEffect(() => {
        let product_url = `/inventory/edit-product/${productId}`;
        
        axios.get(product_url)
        .then(res => {
            setProductNameInput(res.data.name);
            setProductPriceInput(res.data.price);
            setProductCategory(res.data.category);
            setProductTags(res.data.product_tags);
            setProductDesc(res.data.product_desc)
            console.log(res);
        })
    }, [])



                //Change Product Name Input
    const changeProductName = (e) => {
        setProductNameInput(e.target.value);
    }

                //Change Product Price Input
    const changeProductPrice = (e) => {
        setProductPriceInput(e.target.value);
    }
                //Change Product Category
    const changeProductCategory = (e) => {
        setProductCategory(e.target.value);
    }

                //Change Product Tags
    const changeProductTags = (e) => {
        setProductTags(e.target.value);
    }


    const editProduct = (id) => {
        let update_product_url = `/inventory/edit-product/update/${productId}`;
        let new_data = {
            productName: productNameInput,
            productPrice: productPriceInput,
            productCategory: productCategory,
            productTags: productTags,
            productDesc: productDesc
        }
        axios.post(update_product_url, new_data)
        .then(res => {
            if(res.data.nModified > 0) {
                setUpdated(true);
            }
        })
    }

    if(updated) {
        return (
            <div className="admin_container" >
                <AdminNav />
                <div className="admin_main_content" style={{display: "flex", justifyContent: "center", alignItems: "center", flexDirection: "column"}} >
                    <h1> Item has been updated! </h1>
                    <a href="/admin/product/edit-product"> Go Back </a>
                </div>
            </div>
        )
    } else {
        return (
            <div className="admin_container raleway-font " >
            <AdminNav />
            <div className="admin_main_content" >
                <div className="form_outer_container" >
                    <div className="form_inner_container" >
                        <div className="top_form_container" >
                            <h3> Edit Product </h3>
                        </div>
                        <div className="form_container">
                            <input onChange={changeProductName} value={productNameInput} placeholder="Enter your product name" className="add_product_all_inputs" type="text" />
                            <input onChange={changeProductPrice} value={productPriceInput} placeholder="Enter your product price" className="add_product_all_inputs"  type="text" />
                            <select onChange={changeProductCategory} value={productCategory} className="add_product_all_inputs" >
                                <option value="meat" >
                                    Meat
                                </option>
                                <option value="cheese" >
                                    Cheese
                                </option>
                                <option value="vegetable" >
                                    Vegetables
                                </option>
                                <option value="snack" >
                                    Snack
                                </option>
                            </select>
                            <input onChange={changeProductTags} value={productTags} className="add_product_all_inputs"  type="text" placeholder="Enter your product tags" />
                            
                            <textarea value="Hello" value={productDesc} >
    
                            </textarea>
                        </div>
                        <div className="submit_button_container">
                            <button onClick={editProduct.bind(this, productId)} >Add Product</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )
    }
    
}


export default EditProduct;