import React, {useEffect, useState} from "react";
import "../../static/css/Cart/Cart.css";
import axios from "axios";

const CartItem = (props) => {

    const [stockArray, setStockArray] = useState([]);

    const [itemQty, setItemQty] = useState(1);

    const [finalPrice, setFinalPrice] = useState(props.price);

    const [newPrice, setNewPrice] = useState(props.price);

    useEffect(() => {
        for (let i = 0; i < props.stock; i++) {
            setStockArray((state) => [...state, i]);
            console.log(i);
            
        }
    }, [])
    

    const changeQty = (e) => {
        let user_qty = e.target.value;
        setItemQty(user_qty);

        let newPrice = finalPrice * user_qty;

        // setNewPrice(finalPrice * user_qty);


        // console.log(newPrice);

        // props.getUpdatedPrice(newPrice, props.id);

        axios.post("/update-price", {newPrice: newPrice, id: props.id, user_qty: user_qty, itemStock: props.stock})
        .then(res => {
            console.log(res);
        })


    }   


    return (
        <div className="shopping_cart_item">
            <div className="shopping_cart_image"></div>
            <div className="shopping_cart_stock_div" >
                <p> {props.name} </p>
                <small> {props.stock} Left in Stock! </small>
            </div>
            
            <p className="item_price" > ${finalPrice * itemQty} </p>
            <p> 
                <select className="select_input_cart" onChange={changeQty} >
                    {
                        stockArray.map((item) => {
                            return (
                                <option value={item + 1} key={item + 1} > {item + 1} </option>
                            )
                        })
                    }
                {/* <option>1</option>
                <option>2</option>
                <option>3</option>
                <option>4</option> */}
                </select> 
            </p>
            <p className="remove_button_cart" > <a href="#" onClick={props.fetchId.bind(this,props.id)} > Remove Item </a> </p>
        </div>
    )
}


export default CartItem;
