import React, {useEffect} from "react";
import "../../static/css/Admin/BecomeAnAdmin.css";
import axios from "axios";

import { Spinner } from "react-bootstrap";

const BecomeAnAdmin = () => {


    useEffect(() => {
        axios.post("/create-admin-user")
        .then(res => {
            setTimeout(() => {
                window.location.href="/admin/success";
            }, 5000)
        })



    })

    return (
        <div className="become_an_admin_container" >


     
            <div className="grow_animation_container" > 
            
                <Spinner animation="border" role="status">
                    <span className="sr-only">Loading...</span>
                </Spinner>
            </div>

           <div className="bottom_container">
                <p> Please wait your request is being taken up with the manager </p>
           </div>
           

        </div>       
    )
}

export default BecomeAnAdmin;