import React, {Fragment, useEffect, useState} from "react";
import "../../static/css/Cart/Cart.css";
import axios from "axios";
import CartItem from "./CartItem";
import FinalPrice from "./FinalPrice";


const Cart = (props) => {

    const [productDeleteId, setProductDeleteId] = useState();

    const [cartItems, setCartItems] = useState([]);

    const [updatedPrice, setUpdatedPrice] = useState([]);

    const [noCartMessage,setNoCartMessage] = useState();

    const [hasCart, setHasCart] = useState(false);

    useEffect(() => {
      setInterval(() => {
            axios.get("/show-cart")
            .then(res => {
                setCartItems(res.data);
            })


        },1000)


    }, [])


  const getId = (id) => {
      setProductDeleteId(id);

      let data = {
        id: id
      }

      axios.post("/delete-from-cart", data)
      .then(res => {
            
            console.log(res);
          setCartItems(res.data.new_array);
      })
}

    const showCartFunc = () => {
        console.log("Suvject has cart");
    }


    const getUpdatedPrice = (price, id) => {
        let date = Date.now();

        let data = {
            price: price,
            id: id,
            date_created: date
        }
        
        setUpdatedPrice(state =>{
        
            return [...state, {id: data.id, price: data.price, date_created: data.date_created }];
        });

       let new_array = updatedPrice.filter((item,index, self) => 
            index === self.findIndex((t) => (
                t.id === data.id 
            ))
        );

        console.log(new_array)

        

    }

    return (
        <Fragment>

            <div className="cart_container">
                <h2 className="cart_title"> Your Shopping Cart </h2>
                <div className="shopping_cart">
                    {cartItems.map((item) => {
                        return (
                            <CartItem
                            name={item.name}
                            price={item.price}
                            id={item.id}
                            fetchId={getId}
                            stock={item.stock}
                            getUpdatedPrice={getUpdatedPrice}
                              />
                        )
                    })}
                    {
                        hasCart ? showCartFunc() : noCartMessage
                    }

                </div>
                <FinalPrice
                    updatedPrice={updatedPrice}
                />
            </div>
        </Fragment>

    )
}

export default Cart;
