import React, {useEffect, useState} from "react";
import AdminCard from "../AdminCard";
import axios from "axios";

const InventoryTop = (props) => {

    const noBoxShadow = {
        boxShadow: "0px 0px 0px 0px"
    }

    //Number of food items - per category
    const [meatLength, setMeatLength] = useState(0);
    const [snackLength, setSnackLength] = useState(0);
    const [vegetableLength, setVegetableLength] = useState(0);
    const [cheeseLength, setCheeseLength] = useState(0);
    const [fruitLength, setFruitLength] = useState(0);
    //End of Number of food items - per category


    useEffect(() => {
        //Cheese Length
        axios.get("/inventory/cheese")
        .then(res => {
            setCheeseLength(res.data.length);
        })

        //Meat Length
        axios.get("/inventory/meat")
        .then(res => {
            setMeatLength(res.data.length);
        })

        //Vegetable Length
        axios.get("/inventory/vegetable")
        .then(res => {
            setVegetableLength(res.data.length);
        })

        //Snack Length
        axios.get("/inventory/snack")
        .then(res => {
            setSnackLength(res.data.length);
        })

        //Fruit Length
        axios.get("/inventory/fruit")
        .then(res => {
            setFruitLength(res.data.length);
        })




    }, [])

    return (
        <div className="admin_content_part_one all_divs" >
        <AdminCard style={noBoxShadow} > 
            <div className="div1_title admin_main_content_all_top_div cheese_div ">
                <p className="inventoryMainCheeseTop" > Cheese </p>
                <hr />
            </div>
            <div className="div1_main_content" >
                <p className="inventoryMainCheeseLength"> {cheeseLength} </p>
                <p className="inventoryStockCheese inventory_inventory"> cheese in stock </p>
            </div>
        
        </AdminCard>

        <AdminCard style={noBoxShadow} >
            <div className="div2_title admin_main_content_all_top_div" >
                <p> Vegetables </p>
                <hr />
            </div>

            <div className="div2_main_content" >
                <p> {vegetableLength} </p>
                <p className="inventory_inventory"> vegetables in stock </p>
            </div>

        </AdminCard>
        <AdminCard style={noBoxShadow} > 
            <div className="div3_title admin_main_content_all_top_div" >
                <p> Meats </p>
                <hr />
            </div>

            <div className="div3_main_content" >
                <p> {meatLength} </p>
                <p className="inventory_inventory"> meats in stock </p>
            </div>

        </AdminCard>
        <AdminCard style={noBoxShadow} > 
            <div className="div3_title admin_main_content_all_top_div" >
                <p> Snacks </p>
                <hr />
            </div>

            <div className="div3_main_content" >
                <p> {snackLength} </p>
                <p className="inventory_inventory"> snacks in stock </p>
            </div>

        </AdminCard>
        <AdminCard style={noBoxShadow} > 
            <div className="div3_title admin_main_content_all_top_div" >
                <p> Fruits </p>
                <hr />
            </div>

            <div className="div3_main_content" >
                <p> {fruitLength} </p>
                <p className="inventory_inventory"> fruits in length </p>
            </div>

        </AdminCard>
    </div>
    )
}

export default InventoryTop;