import React, {Fragment, useEffect, useState} from "react";
import "../../static/css/Left/LeftSide.css";
import GroceryCard from "./GroceryCards";
import LeftSideNav from "./LeftSideNav";
        // BORDERS ARE IN THE LeftSide.CSS FILE
import axios from "axios";

const LeftSide = () => {

    const [products, setProducts] = useState([]);


    const links = {
        meat: "#",
        cheese: "#",
        vegetable: "#",
        snack: "#"
    }

    useEffect(() => {
        axios.get("/inventory/all")
        .then(res => {
            setProducts(res.data);
        })
    }, [])

    return (
        <Fragment>
            <div className="top_panel_container">
                    {/* LEFT SIDE FIRST HALF */}
                    {/* IMAGE AND NAVIGATION BAR */}
                <div className="top_panel_background_image"></div>
               
                <LeftSideNav links={links} />
            </div>

            {/* LEFT SIDE SECOND HALF */}
            <div className="grocery_items_container">

                  {products.map((item) => {
                    return (
                        <GroceryCard id={item._id} image={item.product_image} stock={item.stock} name={item.name} price={item.price} />
                    )
                  })}
            </div>
        </Fragment>
    )
}


export default LeftSide;
