import React, {useEffect} from "react";
import "../../static/css/Admin/CreateUserSuccess.css"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faThumbsUp } from '@fortawesome/free-solid-svg-icons'



const Success = () => {

    useEffect(() => {
        setTimeout(() => {
            window.location.href="/admin";
        }, 4000)
    })

    return (
        <div className="create_user_success_container" >
            <FontAwesomeIcon icon={faThumbsUp} size="8x" />
            <h4> Success </h4>
            <br />
            <p> You have successfully become an admin! You wil be redirected to the login page shortly! </p>
        </div>
    )
}

export default Success;