import React, {useState, Fragment} from "react";
import AdminNav from "../Part1";
import "../../../static/css/Admin/AddProduct.css";
import {Modal, Button} from "react-bootstrap";

import axios from "axios";

const AddProduct = () => {

    const [productName, setProductName] = useState();

    const [productPrice, setProductPrice] = useState();

    const [productCategory, setProductCategory] = useState();

    const [productTags, setProductTags] = useState();

    const [productDesc, setProductDesc] = useState();

    const [fileImage, setFileImage] = useState();

    const [productStock, setProductStock] = useState();

    const [showModal, setShowModal] = useState(false);

    let data = {
        productName: productName,
        productPrice: productPrice,
        productCategory: productCategory,
        productTags: productTags,
        productDesc: productDesc,
        productStock: productStock

    }

        //Function for changing the state of the product name
    const handleProductNameInput = (e) => {
        let value = e.target.value;
        setProductName(value)
    }

    //Function for changing the state of the product price
    const handleProductPrice = (e) => {
        let value = e.target.value;
        if(isNaN(value)) {
            setProductPrice("");
            alert("Sorry your value has to be a number");
        } else {
            setProductPrice(value);
        }

    }

    //Function for changing the state of the product category
    const handleProductCategory = (e) => {
        let value = e.target.value;
        console.log(value);
        setProductCategory(value);
    }
    
    //Function for changing the state of product tags
    const handleProductTags = (e) => {
        let value = e.target.value;
        setProductTags(value);
    }

    //Function for handling product description
   

    //Function for handling product stock 
    const handleProductStock = (e) => {
        let value = e.target.value;
        if(isNaN(value)) {
            setProductStock("");
            alert("This value has to be a number");
        } else {
            setProductStock(value);
        }

    }

    //Function for handling submission of form
    const handleSubmit = () => {
        //POST VIA AXIOS TO THE POST ROUTE IN SERVER.JS PASS THROUGH DATA
        axios.post("/admin/add-product", data)
        .then(res => {
            console.log(res);
            if(res.data) {
                setShowModal(true);
            }
        })
    }

    const handleClose = () => {
        setShowModal(false);
        setProductStock("");
        setProductPrice("");
        setProductName("");
        setProductCategory("");
        setProductTags("");
    }

    return (
        <Fragment>
                  <Modal show={showModal} onHide={handleClose}>
                        <Modal.Header closeButton>
                        <Modal.Title>Congrats!</Modal.Title>
                        </Modal.Header>
                        <Modal.Body>Your product has been added!</Modal.Body>
                        <Modal.Footer>
                        <Button variant="secondary" onClick={handleClose}>
                            Close
                        </Button>
                        </Modal.Footer>
                    </Modal>
        <div className="admin_container raleway-font " >
            <AdminNav />
        
            <div className="admin_main_content" >
                <div className="form_outer_container" >
                    <div className="form_inner_container" >
                    
                        <div className="top_form_container" >
                            <h3> Add Product </h3>
                        </div>
                        <form enctype="multipart/form-data" method="POST" action="/admin/add-product">
                            <div className="form_container" >
                                <input name="name_input" required onChange={handleProductNameInput} value={productName} placeholder="Enter your product name" className="add_product_all_inputs" type="text" />
                                <input name="product_price" required onChange={handleProductPrice} value={productPrice} placeholder="Enter your product price" className="add_product_all_inputs"  type="text" />
                                <select name="select_input" required onChange={handleProductCategory} value={productCategory} className="add_product_all_inputs" >
                                    <option value="meat" >
                                        Meat
                                    </option>
                                    <option value="cheese" >
                                        Cheese
                                    </option>
                                    <option value="vegetable" >
                                        Vegetables
                                    </option>
                                    <option value="snack" >
                                        Snack
                                    </option>
                                </select>
                                <input name="stock_input" required onChange={handleProductStock} placeholder="Amount In Stock" value={productStock} className="add_product_all_inputs" type="text"  />
                                
                                <div style={{textAlign: "center"}} className="add_product_all_inputs" >
                                
                                    <input name="product_image" required type="file"  />
                                </div>

                                <input name="tag_input" required onChange={handleProductTags} value={productTags} className="add_product_all_inputs"  type="text" placeholder="Enter your product tags" />
                            
                            </div>
                       
                        <div className="submit_button_container">
                            {/* <button onClick={handleSubmit} >Add Product</button> */}
                            <input type="submit" value="Add Product" />
                        </div>
                        </form>
                    </div>
                </div>
            </div>
         </div>
        
      
        </Fragment>
    )
}

export default AddProduct;