import React from "react";


const LeftSideNav = (props) => {
    return (
        <div className="top_panel_nav_container">
            <div>
                <p> <a href={props.links.meat}> Meats </a> </p>
            </div>
            <div>
                <p> <a href={props.links.snack}> Snacks </a> </p>
            </div>
            <div>
                <p> <a href={props.links.cheese}> Cheese </a> </p>
            </div>
            <div>
                <p> <a href={props.links.vegetable}> Vegetables </a> </p>
            </div>
        </div>

    )   
}


export default LeftSideNav;