import React, {useState, useEffect} from "react";
import AdminNav from "../Part1";
import TopPanel from "./InventoryTop";
import Nav from "../../LeftSide/LeftSideNav";
import axios from "axios";

import {Table} from "react-bootstrap";

const EditProduct = () => {


    const [products, setProducts] = useState([]);

    useEffect(() => {
        let url = window.location.pathname + window.location.search;

        switch (url) {
            case "/admin/product/edit-product":
                axios.get("/inventory/all")
                .then(res => {
                    setProducts(res.data);
                })
                break;


            default:
                break;
        }

    }, [])

    return (
        <div className="admin_container raleway-font" >
            <AdminNav />
            <div className="admin_main_content" >
                <TopPanel />
 
                <Table striped bordered hover size="sm">
                    <thead>
                    <tr>
                        <th> Product Name </th>
                        <th> Product Price </th>
                        <th> Product Category </th>
                        <th> Product Stock </th>
                        <th> Edit Product </th>
                    </tr>
                    </thead>
                    <tbody>
                    {products.map((item) => {
                        let edit_url = `/admin/product/edit-product/${item._id}`;
                        return (
                            <tr>
                                <td> {item.name} </td>
                                <td> {item.price} </td>
                                <td> {item.category} </td>
                                <td> {item.stock} </td>
                                <td> <a href={edit_url}> Edit Product </a> </td>
                            </tr>
                        )
                    })}
                    </tbody>
                </Table>
            </div>
        </div>
    )
}

export default EditProduct;