import React, {Fragment} from 'react';

// COMPONENT IMPORTS
import Cart from "./components/Cart/Cart";
import LeftSide from "./components/LeftSide/LeftSide";
import Nav from "./components/Nav/MainNav";
import Admin_Login from "./components/admin/Admin_Login";
import BecomeAnAdmin from "./components/admin/BecomeAnAdmin";
import SuccessAdmin from "./components/admin/CreateUserSucess";
import Admin_Main from "./components/admin/Admin";
import Inventory from "./components/admin/navlinks/Inventory";
import AddProduct from "./components/admin/navlinks/AddProduct";
import Employee from './components/admin/navlinks/Employee';
import PendingOrders from "./components/admin/navlinks/PendingOrders";
import RemoveProduct from "./components/admin/navlinks/RemoveProduct";
import EditProduct from "./components/admin/navlinks/EditProduct";
import EditProductId from "./components/admin/navlinks/EditProductId";
import CompletedOrders from "./components/admin/navlinks/FulfilledOrders";
// COMPONENT IMPORTS END

//React Router
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";
//End of React Router

//CSS Imports
import "./App.css";
    //react bootstrap
import 'bootstrap/dist/css/bootstrap.min.css';

//End of CSS Imports

function App() {
  return (
    <Fragment>
      <Router>
        <Switch>

          <Route path="/admin/pending-orders" component={PendingOrders} />

          <Route path="/admin/fulfilled-orders" component={CompletedOrders} />

          <Route path="/admin/employee" component={Employee} />

          <Route path="/admin/product/add-product" component={AddProduct} />

          <Route path="/admin/product/remove-product" component={RemoveProduct} />

          <Route path="/admin/product/edit-product/:productId" component={EditProductId} />

          <Route path="/admin/product/edit-product" component={EditProduct} />

          <Route path="/admin/inventory/snack" component={Inventory} />

          <Route path="/admin/inventory/meat" component={Inventory} />

          <Route path="/admin/inventory/fruit" component={Inventory}/>

          <Route path="/admin/inventory/vegetable" component={Inventory} />

          <Route path="/admin/inventory" component={Inventory} />

          <Route path="/admin/become-an-admin" component={BecomeAnAdmin} />

          <Route path="/admin/success" component={SuccessAdmin} />

          <Route path="/admin/main" component={Admin_Main} />

          <Route path="/admin" component={Admin_Login}>
          </Route>


          <Route path="/"  >
            <Nav />

            <div className="grocery_main_container" >

              <div className="left_side_container">
              <LeftSide />
              </div>
             <div className="right_side_container">

                <Cart />

             </div>

              </div>

          </Route>


        </Switch>
        
      </Router>
    </Fragment>
  );
}




export default App;
