import React, {useEffect, useState, Fragment} from "react";
import "../../../static/css/Admin/Inventory.css";

//Components Import
import AdminNav from "../Part1";
import AdminCard from "../AdminCard";
import InventoryTop from "./InventoryTop";  
import Nav from "../../LeftSide/LeftSideNav";
import Table from "../Table";
//End of Components Import

import axios from "axios";


const Inventory = () => {


    const [products, setProducts] = useState([]);

    let headNames = {
        one: "Product Name",
        two: "Product Price",
        three: "Product Category",
        four: "Stock"
    }

    useEffect(() => {
        let url = window.location.pathname + window.location.search;

        switch (url) {
            case "/admin/inventory":
                axios.get("/inventory/all")
                .then(res => {
                    setProducts(res.data);
                })
                break;
                
            case "/admin/inventory/meat":
                axios.get("/inventory/meat")
                .then(res => {
                    setProducts(res.data);
                });
                break;
            
            case "/admin/inventory/cheese":
                axios.get("/inventory/cheese")
                .then(res => {
                    setProducts(res.data);
                });
                break;

            case "/admin/inventory/snack":
                axios.get("/inventory/snack")
                .then(res => {
                    setProducts(res.data);
                });
                break;
            
            case "/admin/inventory/vegetable":
                axios.get("/inventory/vegetable")
                .then(res => {
                    setProducts(res.data);
                })
                break;
            default:
                break;
        }

        console.log(url);
    }, [])

    let links = {
        meat: "/admin/inventory/meat",
        snack: "/admin/inventory/snack",
        vegetable: "/admin/inventory/vegetable",
        cheese: "/admin/inventory/cheese"
    }


    return (
        <div className="admin_container">
            <AdminNav />
            <div className="admin_main_content">
                <InventoryTop />
                <Nav links={links} />
                <div className="admin_bottom_container" >
                    <Table headNames={headNames}>
                        {products.map((item) => {
                            return (
                                <tr>
                                    <td> {item.name} </td>
                                    <td> {item.price} </td>
                                    <td> {item.category} </td>
                                    <td> {item.stock} </td>
                                </tr>
                                )
                        })}
                    </Table>
                </div>
            </div>

        </div>
    )
}


export default Inventory;