import React, {useEffect, useState} from 'react';
import Chart from "./Chart";
import LineChart from "./LineChart";
import AdminCard from "./AdminCard";

import axios from "axios";

const AdminMainContent = () => {

    const [pendingOrdersTotal, setPendingOrderTotal] = useState(0);
    const [productsTotal, setProductsTotal] = useState(0);
    const [completeOrdersLength, setCompleteOrdersLength] = useState(0);



    useEffect(() => {
        axios.get("/admin/main/num")
        .then(res => {
            console.log(res);
            setPendingOrderTotal(res.data.length);
        })

        axios.get("/totalProducts")
        .then(res => {
            console.log(res);
            setProductsTotal(res.data.length);
        })

        axios.get("/orders/total/fulfilledOrders")
        .then(res => {
            setCompleteOrdersLength(res.data.length);
        })

    }, [])

    return (
        <div className="admin_content_main_container">
            <div className="admin_content_part_one all_divs" >
                <AdminCard> 
                    <div className="div1_title admin_main_content_all_top_div">
                        <p className="raleway-font" > Orders Made </p>
                        <hr />
                    </div>
                    <div className="div1_main_content" >
                        <p className="raleway-font" > {pendingOrdersTotal} </p>
                        <p className='inventory_inventory'> Orders </p>
                    </div>
                   
                </AdminCard>

                <AdminCard>
                    <div className="div2_title admin_main_content_all_top_div" >
                        <p className="raleway-font" > Orders Pending </p>
                        <hr />
                    </div>

                    <div className="div2_main_content" >
                        <p className="raleway-font" > {pendingOrdersTotal} </p>
                        <p className="raleway-font inventory_inventory" > Orders Pending </p>
                    </div>

                </AdminCard>
                <AdminCard> 
                    <div className="div3_title admin_main_content_all_top_div" >
                        <p className="raleway-font" > Orders Fulfilled </p>
                        <hr />
                    </div>

                    <div className="div3_main_content" >
                        <p className="raleway-font" > {completeOrdersLength} </p>
                        <p className="raleway-font inventory_inventory" > Orders Fulfilled </p>
                    </div>

                </AdminCard>
            </div>


            <div className="admin_content_part_two all_divs">
                <AdminCard style={{width: "60%"}}>
                    <div className="second_part_div1_title" >
                        <p className="raleway-font" > Products </p>
                    </div>

                    <div className="second_part_div1_main_content" >
                        <p className="raleway-font" > {productsTotal} </p>
                        <p className="raleway-font" > Products </p>
                    </div>

                </AdminCard>
            </div>


            <div className="admin_content_part_three all_divs" >
                <AdminCard style={{width: "50%", height: "100%", borderRadius: "0%"}}>
                   <Chart />
    
                </AdminCard>
                <AdminCard style={{width: "50%", height: "100%", borderRadius: "0%"}}>
                   <LineChart />
                </AdminCard>
            </div>

        </div>
    )
}

export default AdminMainContent;