import React, {useEffect, useState, Fragment} from "react";
import "../../static/css/Admin/Admin_Login.css";
import axios from "axios";

import {Alert, variant} from "react-bootstrap";

const Admin_Login = () => {

    const [userInputTag, setUserInputTag] = useState("");
    const [userPasswordTag, setUserPasswordTag] = useState("");

    //Error Message
    const [variant, setVariant] = useState("danger");
    const [errorMessage, setErrorMessage] = useState();


    //Use Effect Function
    useEffect(() => {
        axios.get("/status")
        .then(res => {
            console.log(res);
            if(res.data.message == "User set") {
                let becomeAdminLink = document.getElementById("becomeAnAdmin");
                becomeAdminLink.style.display = "none";
                setUserInputTag(res.data.username);
                setUserPasswordTag(res.data.password);
            } else {
                setUserInputTag();
                setUserPasswordTag();
            }
        })
    }, [])


        //Function for disappearing error message
        const disappear = () => {
            setTimeout(() => {
                let error_div = document.getElementById("alertDiv");
                error_div.style.display = "none";
                setErrorMessage();
            }, 4000)
        }

        //Function for validating whether or not someone is an admin
    const handleClick = () => {
        let data = {
            username: userInputTag,
            password: userPasswordTag
        }
        axios.post("/validate-admin", data)
        .then(res => {
            console.log(res);

            let response_message = res.data;
            let error_alert = document.getElementById("alertDiv");
            switch (response_message) {
                case "Password has been confirmed":
                    window.location.href="/admin/main";
                    break;
                case "Password is incorrect":
                    console.log("Password is incorrect");
                    error_alert.style.display = "block";
                    setErrorMessage("Your password is incorrect please try again");
                    disappear();
                    break;
                case "Username isn't correct":
                    error_alert.style.display = "block";
                    setErrorMessage("Your username is incorrect please try again");
                    disappear();
                    break;
                default:
                    break;
            }

        })
    }

        //Function for making someone an admin
    const becomeAnAdmin = () => {
        window.location.href="/admin/become-an-admin";
    }

        //Setting Username Input
    const handleUserInput = (e) => {
        let input = e.target.value;

        setUserInputTag(input);

    }
        //Setting Password Input

    const handlePasswordInput = (e) => {
        let password_input = e.target.value;

        setUserPasswordTag(password_input)
    }



    return (
     <Fragment>
           <Alert id="alertDiv" style={{display: "none"}} variant={variant}>
                {errorMessage}
            </Alert>
        <div className="admin_page_login_container">
            <div className="admin_login_main_div">

                <div className="admin_login_top_panel">
                    <h4> Admin Login </h4>
                </div>
                <hr />

                <div className="admin_login_second_half">

                    <div>
                        <label> Username: </label>
                        <input onChange={handleUserInput} value={userInputTag} type="text" placeholder="Enter your username" />
                    </div>

                    <div>
                        <label> Password: </label>
                        <input onChange={handlePasswordInput} value={userPasswordTag} type="password" placeholder="Enter your password" />
                    </div>

                    <div>
                        <button onClick={ handleClick } > Login </button>
                    </div>
                  
                </div>
            </div>
        <div className="admin_login_links_container" >
            <a className="admin_login_bottom_links" href="/" > Go Back </a>
            <a href="#" className="admin_login_bottom_links" id="becomeAnAdmin" onClick={becomeAnAdmin} > Want to become an admin? </a>
        </div>
          
        </div>

    </Fragment>
    )
}

export default Admin_Login;