import React, {Fragment, useEffect, useState} from "react";
import { Doughnut } from "react-chartjs-2";

import axios from "axios";

const ChartsDiv = () => {

    const [fruitTotal, setFruitTotal] = useState();
    const [veggieTotal, setVeggieTotal] = useState();
    const [meatTotal, setMeatTotal] = useState();
    const [cheeseTotal, setCheeseTotal] = useState();
    const [snackTotal, setSnackTotal] = useState();

    const options = {
        title: "Products Sold",
        hAxis: { title: "Time", viewWindow: { min: 0, max: 15 } },
        vAxis: { title: "Products", viewWindow: { min: 0, max: 50 } },
        legend: "none"
    }

    useEffect(() => {
        axios.get("/products/total/cheese")
        .then(res => {
            setCheeseTotal(res.data.length);
        })

        axios.get("/products/total/veggie")
        .then(res => {
            setVeggieTotal(res.data.length);
        })

        axios.get("/products/total/snack")
        .then(res => {
            setSnackTotal(res.data.length);
        })

        axios.get("/products/total/meats")
        .then(res => {
            setMeatTotal(res.data.length);
        })

        axios.get("/products/total/fruit")
        .then(res => {
            setFruitTotal(res.data.length);
        })

    }, [])

    const data = {
        datasets: [{
            label: "# of products sold",
            data: [meatTotal,cheeseTotal,snackTotal,veggieTotal,fruitTotal],
            backgroundColor: ["red", "yellow", "royalblue", "green", "orange"],
            weight: 2
        }],
        legend: "Products sold",
        labels: [
            "Meat",
            "Cheese",
            "Snacks",
            "Vegetables",
            "Fruit"
        ]
    }

    return (
        <Fragment>
            <Doughnut
            data={data}
            width={100}
            height={50}
            options={{ maintainAspectRatio: false }}
        />
      </Fragment>
    )
}

export default ChartsDiv;