import React, {useEffect, useState, Fragment} from "react";
import axios from "axios";

import {PayPalButton} from "react-paypal-button-v2";

import {Modal, Button} from "react-bootstrap";


//CSS import
import "../../static/css/Cart/FinalPrice.css";

const FinalPrice = (props) => {

    const payment_div_styles = {
        textAlign: "center"
    }

    const [finalPrice, setFinalPrice] = useState(0);

    const [updatedPrice, setUpdatedPrice] = useState([]);

    const [isAltered, setIsAltered] = useState(false);

    const [showSuccessModal, setShowSuccessModal] = useState(false);


    const showUpdatedPrice = () => {
      let updated_price = [];

      updatedPrice.map(item => {
        updated_price.push(item.price);
      })
    
      let new_price = updated_price.reduce((first, second) => {
        return first + second;
      })
      console.log(updated_price);

      console.log(new_price);
      

      return new_price;
    }

    const closeModal = () => {
      setShowSuccessModal(false);
      window.location.reload();
    }

    const handlePaymentSuccess = () => {

      let data = {
        itemsList: updatedPrice,
        
      }

      if(isAltered) {
        data.itemsList = updatedPrice; 
      } else {
        data.itemsList = finalPrice;
      }

      axios.post("/payment-success", data) 
      .then(res => {
        console.log(res);
        if(res.data) {
          setShowSuccessModal(true);
        } 
      })

      alert(true);
    }

     let paypalButtonStyles = {
       layout: "horizontal"
     }
   
    useEffect(() => {

      setInterval(() => {


        axios.get("/updated_price")
        .then(res => {
            if(res.data.length === 0) {
              axios.get("/final-price")
              .then(secRes => {
                  setFinalPrice(secRes.data.final_price);
                  console.log("This is the second Result");
                  setIsAltered(false);
                  console.log(secRes.data);
              })
            } else {
              
              setUpdatedPrice(res.data);
              setIsAltered(true);
              
            }
        })

      }, 1000)



}, [])

if(window.innerWidth <= 740) {
  paypalButtonStyles.layout = "horizontal";
  
} 
  
    return (
      <Fragment>

      <Modal show={showSuccessModal} onHide={closeModal}>
        <Modal.Header closeButton>
          <Modal.Title>Success!</Modal.Title>
        </Modal.Header>
        <Modal.Body>Congrats, your payment was successful!</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={closeModal}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>

        <div style={payment_div_styles} >
          {/* Total Price: ${finalPrice} */}
          <p className="total_price_para_tag" >Total Price: ${ isAltered ? showUpdatedPrice() : finalPrice } </p>
          
          <div className="paypalButtonDiv" >
            <PayPalButton 
                style={paypalButtonStyles}
                amount={ isAltered ? showUpdatedPrice() : finalPrice }
                onSuccess={handlePaymentSuccess}
                options={{
                  clientId: "AfPASh5_oqtWHrLGHk80drz_69H4ggIx75QBpEaTvcYopdlraeVux_ezwKfsmyDf4vYSnOsYeYG9BfVd"
                }}
            />
          </div>

        </div>
      </Fragment>
    )
}



export default FinalPrice;
