import React from "react";

import {Table} from "react-bootstrap";

const ReuseTable = (props) => {
    return (
        <Table striped bordered hover size="sm">
        <thead>
          <tr>
            <th> {props.headNames.one} </th>
            <th> {props.headNames.two} </th>
            <th> {props.headNames.three} </th>
            <th> {props.headNames.four} </th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>1</td>
            <td>Mark</td>
            <td>Otto</td>
            <td>@mdo</td>
          </tr>
         {props.children}
        </tbody>
      </Table>
    )
}



export default ReuseTable;