import React, {useEffect, useState, Fragment} from "react";
import AdminNav from "../Part1";
import {Table, Modal, Button} from "react-bootstrap";
import axios from "axios";

import "../../../static/css/Admin/PendingOrders.css";

const PendingOrders = () => {

    const [pendingOrders, setPendingOrders] = useState([]);

    const [showModal, setShowModal] = useState(false);

    useEffect(() => {
        axios.get("/orders/pending")
        .then(res => {
            console.log(res.data);
            setPendingOrders(res.data);
        })
    }, [])

    const handleClose = () => {
        setShowModal(false);
        window.location.reload();
    }


    const completeOrder = (id) => {

        let data = {
            id: id
        }

        axios.post("/orders/fulfill-order", data)
        .then(res => {
            console.log(res);

            if(res.data) {
                setShowModal(true);
            }

        })
    }
    

    return (
    <Fragment>


        <Modal
        show={showModal}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
          <Modal.Title>Order has been marked as completed!</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          The order has been successfully marked as completed!
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            Close
          </Button>
        </Modal.Footer>
      </Modal>
        <div className="admin_container" >
            <AdminNav />
            <div className="admin_main_content" >
                <div className="pendingOrdersTitleDiv" >
                    <h1>
                        Pending Orders
                    </h1>
                </div>
                {/* //Insert Table Showing Number of Pending Orders */}
                <Table striped bordered hover>
                    <thead>
                        <tr>
                        <th>Product Id</th>
                        <th>Qty</th>
                        <th>Price Paid</th>
                        <th>Session Id</th>
                        <th> Order Fulfilled </th>
                        </tr>
                    </thead>
                    <tbody>
                       

                        {
                            pendingOrders.map((item) => {
                                return (
                                    <tr>
                                        <td> {item.product_id} </td>
                                        <td> {item.qty} </td>
                                        <td> {item.price} </td>
                                        <td> {item.ssn_id} </td> 
                                        <td> <a href="#" onClick={completeOrder.bind(this,item.ssn_id)} > Mark as Completed </a> </td>
                                    </tr>
                                )
                            })
                        }

                    </tbody>
                </Table>
            </div>
        </div>
    </Fragment>
    )
}


export default PendingOrders;



