import React, {Fragment} from "react";
import { Navbar } from "react-bootstrap";
import "../../static/css/Nav/MainNav.css";

const Nav = () => {
    return (
    <Fragment>
        <Navbar className="navBarMain" >
        <Navbar.Brand href="#home" className="nav-links brand" >Welcome to T-Shop</Navbar.Brand>
        <Navbar.Toggle />
        <Navbar.Collapse className="justify-content-end "   >
            <Navbar.Text className="nav-links" >
            Shop Now TEO
            </Navbar.Text>
        </Navbar.Collapse>
        </Navbar>
    </Fragment>
    )   
}

export default Nav;