import React, {useEffect} from "react";
import "../../static/css/Admin/Admin.css";
import axios from "axios";
import Part1 from "./Part1";


//Components Import

import AdminNav from "./AdminNav";
import Admin_Content from "./AdminMainContent";
//End of Components Import

const Admin = () => {

    useEffect(() => {
        axios.get("/status")
        .then(res => {
            let response = res.data.session_status.isSet;
            if(!response) {
                console.log("UnComment me");
                //Uncomment later 
                // window.location.href="/";
            }
        })
    })

    return (
        <div className="admin_container">
            
            <Part1 />

            <div className="admin_main_content">
                <Admin_Content />
            </div>
        </div>
    )
}


export default Admin;